.SideDrawer {
    position: fixed;
    width: 280px;
    top: 0;
    left: 0;
    max-width: 70%;
    height: 100%;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition:transform 0.3s ease-out;
}

@media (min-width:500px){
    .SideDrawer{
        display: none;
    }
}
.Open{
    transform: translateY(0);
}
.Close{
    transform: translateY(-100%);
}
.Logo{
    height: 11%;
    margin-bottom: 32px;
}